/*
 ajax
*/
import ajax from './ajax'
// const BASE_URL = 'http://local:4000'
const BASE_URL = 'http://localhost:8090/api'
/**
 * ()
 * urlquery
 */
export const reqAddress = geohash => ajax(`${BASE_URL}/position/${geohash}`)
/**
 *  msite 
 */
export const reqCategorys = () => ajax(BASE_URL + '/food_category')
/**
 *  msite (query)
 * 
 * ajax  
 */
export const reqShops = ({latitude, longitude}) => ajax(BASE_URL + '/shops', {latitude, longitude})
/**
 * 
 */
export const reqSearchShop = (geohash, keyword) => ajax(BASE_URL + '/search_shops', {geohash, keyword})
/**
 * 
 */
export const reqPwdLogin = (name, pwd, id, value) => ajax(BASE_URL + '/login_pwd', {name, pwd, id, value}, 'POST')
/**
 * 
 */
export const reqSendCode = phone => ajax(BASE_URL + '/sendcode', {phone})
/**
 * 
 */
export const reqSmsLogin = (phone, code) => ajax(BASE_URL + '/login_sms', {phone, code}, 'POST')

export const reqCaptcha = () => ajax(BASE_URL + '/captcha')

/**
 * ()
 */
export const reqUserInfo = () => ajax(BASE_URL + '/userinfo')

/**
 * 
 */
export const reqLogout = () => ajax(BASE_URL + '/logout')
/**
 * (mock )
 */
export const reqShopInfo = () => ajax('/info')
/**
 * 
 */
export const reqShopRatings = () => ajax('/ratings')
/**
 * 
 */
export const reqShopGoods = () => ajax('/goods')

export const uploadFile = (formData) => ajax(BASE_URL + '/upload/avator', formData, 'POST')



